body {
  background-image: url(https://luminari.art/img/Frozen/frozen_top.png), url(https://luminari.art/img/Frozen/frozen_bottom.png), url(https://luminari.art/img/Frozen/frozen_background.png);
  background-repeat: no-repeat;
  background-size: 50%, 100% 70%, cover;
  background-position: top left, bottom, center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscal
}

html{
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Gomarice";
  src: url('./fonts/Gomarice.ttf') format("truetype");
}